<template>
    <div>
        <v-container
            class="pa-0 overflow-hidden"
        >
            <v-row
                class="d-flex flex-row flex-nowrap flex-md-wrap scroll-horizontal lista-imagens"
                no-gutters
                @mousedown="this.pararScrollAutomatico"
            >
                <v-col
                    v-for="(imagemEvento, indice) in imagensEventos"
                    :key="indice"
                    cols="8"
                    md="4"
                    class="scroll-snap"
                >
                    <v-img
                        :src="`${imagemEvento.src}`"
                        :lazy-src="`${imagemEvento.src}`"
                        @click.stop="destacarImagemClicada(imagemEvento)"
                        class="fill-height"
                        aspect-ratio="0.75"
                        eager
                    ></v-img>
                </v-col>
            </v-row>
        </v-container>
        <v-dialog
            elevation="0"
            v-model="dialog"
            content-class="ma-0"
            overlay-color="#fff"
            overlay-opacity="1"
            scrollable
        >
            <v-img
                elevation="0"
                v-if="imagemDestacada"
                contain
                :src="`${ imagemDestacada.src }`"
                @click="fecharModalImagemDestacada"
                class="transparent"
            ></v-img>
        </v-dialog>
    </div>

</template>

<script>
export default {
    methods: {
        destacarImagemClicada: function(imagemClicada) {
            if (imagemClicada) { 
                this.dialog = true;
                this.imagemDestacada = imagemClicada;
            }
        },
        fecharModalImagemDestacada: function() {
            this.dialog = false;
        },
        executarFrameDaAnimacao: function() {
            const listaImagens = document.querySelector(".lista-imagens");
            if (listaImagens.scrollLeft == listaImagens.scrollWidth - window.innerWidth) {
                this.pararScrollAutomatico();
            } else {
                listaImagens.scrollLeft++;
                this.idAnimacaoScroll = window.requestAnimationFrame(this.executarFrameDaAnimacao);
            }
        },
        scrollHorizontalListaImagens: function() {
            window.requestAnimationFrame(this.executarFrameDaAnimacao);
        },
        pararScrollAutomatico: function() {
            window.cancelAnimationFrame(this.idAnimacaoScroll);
            this.idAnimacaoScroll = undefined;
        }
    },
    mounted() {
        if (!document.querySelector(".layout-desktop")) {
            this.scrollHorizontalListaImagens();
        }
    },
    beforeDestroy() {
        this.pararScrollAutomatico();
    },
    data: function() {
        return {
            idAnimacaoScroll: undefined,
            dialog: false,
            imagemDestacada: null,
            imagensEventos: [
                {
                    "src": "../img/imagens-eventos/1.jpg",
                    "legenda": "Lorem ipsum dolor sit amet, consectetur adipiscing elit."
                },
                {
                    "src": "../img/imagens-eventos/2.jpg",
                    "legenda": "Lorem ipsum dolor sit amet, consectetur adipiscing elit."
                },
                {
                    "src": "../img/imagens-eventos/3.jpg",
                    "legenda": "Lorem ipsum dolor sit amet, consectetur adipiscing elit."
                },
                {
