import Vue from 'vue'
import VueRouter from 'vue-router'

import App from './App.vue'

import Home from './components/paginas/Home.vue'
import Cerimonial from './components/paginas/Cerimonial.vue'
import Sobre from './components/paginas/Sobre.vue'

import vuetify from './plugins/vuetify';

// Vue.config.productionTip = false

Vue.use(VueRouter)

const router = new VueRouter({
    mode: 'history',
    routes: [
        { path: '/', component: Home },
        { path: '/cerimonial', component: Cerimonial },
        { path: '/sobre', component: Sobre }
    ]
})

new Vue({
    router,
    vuetify,
    render: h => h(App)
}).$mount('#app')