            const metadeDaQuantidade = Math.ceil(this.depoimentos.length / 2)
            return this.depoimentos.filter(depoimento => {
                if (this.depoimentos.indexOf(depoimento) > metadeDaQuantidade ||
                    this.depoimentos.indexOf(depoimento) == metadeDaQuantidade) {
